import React from "react";

function Row(props) {
  return (
    <div>
      <ul>
        {props.results.map((result) => (
          <li className="list-group-item" key={result.phone}>
            <img
              alt={result.name.first}
              className="img-fluid"
              src={result.picture.thumbnail}
            />
          </li>
        ))}
      </ul>
      <ul>
        {props.results.map((result) => (
          <li className="list-group-item" key={result.phone}>
            {result.name.first} {result.name.last}
          </li>
        ))}
      </ul>

      <ul>
        {props.results.map((result) => (
          <li className="list-group-item" key={result.phone}>
            {result.gender}
          </li>
        ))}
      </ul>

      <ul>
        {props.results.map((result) => (
          <li className="list-group-item" key={result.phone}>
            {result.location.country}
          </li>
        ))}
      </ul>
      <ul>
        {props.results.map((result) => (
          <li className="list-group-item" key={result.phone}>
            {result.dob.age}
          </li>
        ))}
      </ul>
    </div>
  );
}

export default Row;
